import React, { useMemo } from 'react';
import Markdown from 'react-markdown';
import cx from 'classnames';
import Rings from '../components/Ring';
import styles from '../css/about.module.css';
import Contact from './Contact';
import Notice from './Notice';
import { about } from '../utils/text';
import { parseHtml, RouterLink } from '../utils/plugins';

export default function About() {
	
	const boxes = useMemo(() => {
		if (!Array.isArray(about.cards)) return null;
		return about.cards.map((meta: { content: string, img: string }, i) => {
			return <div
				key={cx('img', i)}
				className={styles.box}
				data-aos='fade-up'
				data-aos-delay={i * 100}
			>
				<img src={process.env.PUBLIC_URL + meta.img} />
				{meta.content}
			</div>;
		});
	}, []);

	const businesses = useMemo(() => {
		return (
			<section id='businesses'>
				<div className='wrapper'>
					<h2 data-aos='fade-left'>{about.cardSection.h2}</h2>
					<div className={styles.boxContainer}>{boxes}</div>
					<p data-aos='fade-left'>{about.cardSection.sub}</p>
				</div>
			</section>
		);
	}, [boxes]);

	return <>
		<section id='industries'>
			<div className='wrapper'>
				{Object.values(about.header).map((v, i) => {
					return <h2 data-aos='fade-left' key={cx('header', i)}>{v}</h2>;
				})}
			</div>
			<Rings className='bgRings' />
		</section>
		<section id={styles.started}>
			<div className='wrapper'>
				<div className={styles.columns}>
					<img className={cx(styles.arrow)} style={{
						stroke: 'var(--blue)',
						filter: 'hue-rotate(180deg) brightness(0.5) saturate(3.0)',
						transform: 'scaleX(-1)'
					}} src={process.env.PUBLIC_URL + '/icons/arrow-left.svg'} />
					<div className={styles.box}>
						<div className={styles.head}>
							{about.started.title}
						</div>
						<div className={styles.description}>
							<Markdown 
								source={about.started.description}
								escapeHtml={false}
								astPlugins={[parseHtml]}
								renderers={{
									link: RouterLink
								}}
							/>
						</div>
					</div>
					<div className={styles.imgContainer}>
						<img src={process.env.PUBLIC_URL + '/images/frame-1.png'} />
						<img src={process.env.PUBLIC_URL + '/images/frame-2.png'} />
						<img src={process.env.PUBLIC_URL + '/images/frame-3.png'} />
					</div>
				</div>
			</div>
		</section>
		<section className={styles.divider} />
		<section id={styles.impact}>
			<div className='wrapper'>
				<div className={styles.columns}>
					<div className={styles.box}>
						<div className={styles.head}>
							{about.impact.title}
						</div>
						<div className={styles.description}>
							<Markdown 
								source={about.impact.description}
								escapeHtml={false}
								astPlugins={[parseHtml]}
								renderers={{
									link: RouterLink
								}}
							/>
						</div>
					</div>
				</div>
			</div>
		</section>
		<section className={styles.divider} />
		<section id={styles.data}>
			<div className='wrapper'>
				<div className={styles.columns}>
					<img className={cx(styles.arrow, 'shadowed')} src={process.env.PUBLIC_URL + '/icons/arrow-left.svg'} />
					<div className={styles.column}>
						{Object.values(about.data.boxes).map((data, i) => {
							let {title, description } = data as unknown as { title: string, description: string };
							return <div className={styles.box} key={cx('sub', i)}>
								<h2>{title}</h2>
								<Markdown 
									source={description}
									escapeHtml={false}
									astPlugins={[parseHtml]}
									renderers={{
										link: RouterLink
									}}
								/>
							</div>;
						})}
					</div>
					<div className={styles.box}>
						<div className={styles.head}>
							{about.data.title}
						</div>
						<div className={styles.description}>
							<Markdown 
								source={about.data.description}
								escapeHtml={false}
								astPlugins={[parseHtml]}
								renderers={{
									link: RouterLink
								}}
							/>
						</div>
					</div>
				</div>
			</div>
		</section>
		<section id='action'>
			<div className='wrapper'>
				{Object.values(about.action).map((v, i) => {
					return <h1 data-aos='fade' key={cx('action', i)}>{v}</h1>;
				})}
			</div>
		</section>
		<Notice />
		<Contact />
	</>;

}